import {Typography} from "@material-ui/core"
import Avatar from "@material-ui/core/Avatar"
import Container from "@material-ui/core/Container"
import CssBaseline from "@material-ui/core/CssBaseline"
import {makeStyles} from "@material-ui/core/styles"
import LockOutlinedIcon from "@material-ui/icons/LockOutlined"
import React, {useCallback, useMemo, useState} from "react"
import {BasePageProps} from "../../App"
import FormComponent, {FormItem} from "../../components/form/form"
import PageComponent from "../../components/page"
import WrongPageComponent from "../../components/wrong-page"
import {
    AllUserInfoFragment,
    MeDocument,
    MutationUpdateAccountArgs,
    useUpdateAccountMutation,
} from "../../graphql/schema.generated"
import {removeNested} from "../../services/utils"

const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
}))

export interface AccountUpdatePageViewProps {
    onSubmit: (
        form: MutationUpdateAccountArgs & {
            password?: {confirmPassword: string}
        },
    ) => void
    user: AllUserInfoFragment
    isPasswordUpdate: boolean
    changeIsPasswordUpdate: (isPasswordUpdate: boolean) => void
}
export const AccountUpdatePageView: React.FC<AccountUpdatePageViewProps> = ({
    onSubmit,
    isPasswordUpdate,
    changeIsPasswordUpdate,
    user: {address, latitude, longitude, type, firstName, lastName},
}) => {
    const classes = useStyles()

    const formItems = useMemo(
        () =>
            [
                {
                    type: "text",
                    textType: "password",
                    required: isPasswordUpdate,
                    alignment: {xs: 12},
                    id: ".password.oldPassword",
                    label: "Old Password",
                    autoComplete: "password",
                },

                {
                    type: "text",
                    textType: "password",
                    required: isPasswordUpdate,
                    alignment: {xs: 12},
                    id: ".password.newPassword",
                    label: "New Password",
                    autoComplete: "password",
                },

                {
                    type: "text",
                    textType: "password",
                    required: isPasswordUpdate,
                    alignment: {xs: 12},
                    id: ".password.confirmPassword",
                    label: "Confirm Password",
                    autoComplete: "password",
                },

                {
                    type: "text",
                    required: true,
                    defaultValue: firstName,
                    alignment: {xs: 12, sm: 6},
                    id: ".firstName",
                    label: "First Name",
                    autoComplete: "fname",
                },

                {
                    type: "text",
                    required: true,
                    defaultValue: lastName,
                    alignment: {xs: 12, sm: 6},
                    id: ".lastName",
                    label: "Last Name",
                    autoComplete: "lname",
                },

                ...(type === "Submitter"
                    ? [
                          {
                              defaultValue: {
                                  properties: {
                                      display_name: address,
                                  },
                                  geometry: {
                                      coordinates: [longitude, latitude],
                                  },
                              },
                              freeSolo: true,
                              type: "location",
                              required: true,
                              alignment: {xs: 12},
                              id: ".address",
                              label: "Shipping Address",
                              locationIds: {
                                  latitude: ".geolocation.latitude",
                                  longitude: ".geolocation.longitude",
                              },
                              hint:
                                  "address 3D printer owners should send parts",
                              autoComplete: "address",
                          },
                      ]
                    : []),

                {
                    type: "location",
                    required: true,
                    alignment: {xs: 12},
                    id: type === "Submitter" ? ".city" : ".address",
                    hint: "city your are based to find hospitals nearby",
                    locationIds: {
                        latitude: ".geolocation.latitude",
                        longitude: ".geolocation.longitude",
                    },
                    defaultValue: {
                        properties: {
                            display_name: address,
                        },
                        geometry: {
                            coordinates: [longitude, latitude],
                        },
                    },

                    autoComplete: "address-level2",
                    label: "City",
                },
            ] as FormItem[],
        [
            address,
            firstName,
            isPasswordUpdate,
            lastName,
            latitude,
            longitude,
            type,
        ],
    )

    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Update Account
                </Typography>

                <FormComponent
                    submitButtonTitle="Update Account"
                    formItems={formItems}
                    onUpdate={(id, value) => {
                        if (id === ".password.oldPassword") {
                            changeIsPasswordUpdate(!!value)
                        }
                    }}
                    onSubmit={onSubmit}></FormComponent>
            </div>
        </Container>
    )
}

export interface AccountUpdatePageProps extends BasePageProps {}
export const AccountUpdatePage: React.FC<AccountUpdatePageProps> = ({
    updateSnack,
}) => {
    const [isPasswordUpdate, changeIsPasswordUpdate] = useState(false)

    const [mutate] = useUpdateAccountMutation({
        refetchQueries: [{query: MeDocument}],
        onCompleted: data => {
            if (data.updateAccount) {
                updateSnack({
                    message: "successfully updated account",
                    severity: "success",
                })
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
    })

    const onSubmitAccount = useCallback(
        (args: MutationUpdateAccountArgs) => {
            mutate({
                variables: removeNested(args, ".password.confirmPassword"),
            })
        },
        [mutate],
    )

    const onSubmit = useCallback(
        async (
            args: MutationUpdateAccountArgs & {
                password?: {confirmPassword: string}
            },
        ) => {
            if (isPasswordUpdate && args.password) {
                if (
                    args["password"]["newPassword"] ===
                    args["password"]["confirmPassword"]
                ) {
                    onSubmitAccount(args)
                } else {
                    updateSnack({message: "passwords do not match"})
                }
            } else {
                args.password = undefined
                onSubmitAccount(args)
            }
        },
        [onSubmitAccount, updateSnack, isPasswordUpdate],
    )

    const updatePage = useCallback(
        (user: AllUserInfoFragment) => {
            return (
                <AccountUpdatePageView
                    changeIsPasswordUpdate={changeIsPasswordUpdate}
                    user={user}
                    isPasswordUpdate={isPasswordUpdate}
                    onSubmit={onSubmit}
                />
            )
        },
        [isPasswordUpdate, onSubmit],
    )

    return (
        <PageComponent
            showFooter={true}
            title="Update Account"
            rolePages={[
                {
                    component: updatePage,
                    role: "Volunteer",
                },
                {
                    component: updatePage,
                    role: "Submitter",
                },
            ]}>
            <WrongPageComponent text={"Must be signed in to update account"} />
        </PageComponent>
    )
}

export default AccountUpdatePage
