import {
    ButtonBase,
    createStyles,
    makeStyles,
    Theme,
    Typography,
} from "@material-ui/core"
import React from "react"

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            display: "flex",
            flexWrap: "wrap",
            minWidth: 300,
            width: "100%",
        },
        image: {
            position: "relative",
            height: "40vh",
            [theme.breakpoints.down("xs")]: {
                width: "100% !important", // Overrides inline-style
                height: 100,
            },
            "&:hover, &$focusVisible": {
                zIndex: 1,
                "& $imageBackdrop": {
                    opacity: 0.5,
                },
                "& $imageMarked": {
                    opacity: 0,
                },
                "& $imageTitle": {
                    border: "4px solid currentColor",
                },
            },
        },
        focusVisible: {},
        imageButton: {
            position: "absolute",
            left: 0,
            right: 0,
            top: 0,
            bottom: 0,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            color: theme.palette.common.white,
            fontWeight: 50,
        },
        imageSrc: {
            position: "absolute",
            left: 0,
            right: 0,
            top: 0,
            bottom: 0,
            backgroundSize: "cover",
            backgroundPosition: "center 40%",
        },
        imageBackdrop: {
            position: "absolute",
            left: 0,
            right: 0,
            top: 0,
            bottom: 0,
            backgroundColor: theme.palette.common.black,
            opacity: 0.6,
            transition: theme.transitions.create("opacity"),
        },
        imageTitle: {
            position: "relative",
            padding: `${theme.spacing(2)}px ${theme.spacing(
                4,
            )}px ${theme.spacing(1) + 6}px`,
        },
        imageMarked: {
            height: 3,
            width: 18,
            backgroundColor: theme.palette.common.white,
            position: "absolute",
            bottom: -2,
            left: "calc(50% - 9px)",
            transition: theme.transitions.create("opacity"),
        },
    }),
)

export interface PictureButtonProps {
    url: string
    title: string
    width: string
    href: string
}

const PictureButton = ({url, title, width, href}: PictureButtonProps) => {
    const classes = useStyles()

    return (
        <ButtonBase
            href={href}
            focusRipple
            key={title}
            className={classes.image}
            focusVisibleClassName={classes.focusVisible}
            style={{
                height: 200,

                width: width,
            }}>
            <span
                className={classes.imageSrc}
                style={{
                    backgroundImage: `url(${url})`,
                }}
            />
            <span className={classes.imageBackdrop} />
            <span className={classes.imageButton}>
                <Typography
                    component="span"
                    variant="h4"
                    color="inherit"
                    className={classes.imageTitle}>
                    {title}
                    <span className={classes.imageMarked} />
                </Typography>
            </span>
        </ButtonBase>
    )
}

export default PictureButton
