import Step from "@material-ui/core/Step";
import StepConnector from "@material-ui/core/StepConnector";
import { StepIconProps } from "@material-ui/core/StepIcon";
import StepLabel from "@material-ui/core/StepLabel";
import Stepper from "@material-ui/core/Stepper";
import {
  createStyles,
  makeStyles,
  Theme,
  withStyles
} from "@material-ui/core/styles";
import clsx from "clsx";
import React, { useCallback, useEffect, useMemo } from "react";
import theme from "./theme";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: "100%"
    },
    button: {
      marginRight: theme.spacing(1)
    },
    instructions: {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1)
    }
  })
);

export interface StepperStep {
  label: string;
  icon: React.ReactElement;
}

export interface CustomizedSteppersProps {
  steps: StepperStep[];
  currentStep: number;
  colors?: { color1: string; color2: string; color3: string };
}

export default function CustomizedSteppers({
  steps,
  currentStep,
  colors: { color1, color2, color3 } = {
    color1: theme.palette.primary.main,
    color2: theme.palette.primary.main,
    color3: theme.palette.primary.main
  }
}: CustomizedSteppersProps) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(1);

  const ColorLibConnector = useMemo(() => {
    return withStyles({
      alternativeLabel: {
        top: 22
      },
      active: {
        "& $line": {
          backgroundImage: `linear-gradient( 95deg,${color1} 0%,${color2} 50%,${color3} 100%)`
        }
      },
      completed: {
        "& $line": {
          backgroundImage: `linear-gradient( 95deg,${color1} 0%,${color2} 50%,${color3} 100%)`
        }
      },
      line: {
        height: 3,
        border: 0,
        backgroundColor: "#eaeaf0",
        borderRadius: 1
      }
    })(StepConnector);
  }, [color1, color2, color3]);

  const icons: { [index: string]: React.ReactElement } = useMemo(() => {
    const object: { [key: number]: React.ReactElement } = {};
    steps.forEach((step, i) => {
      object[i + 1] = step.icon;
    });
    return object;
  }, [steps]);

  const ColorLibStepIcon = useCallback(
    (props: StepIconProps) => {
      const classes = makeStyles({
        root: {
          backgroundColor: "#ccc",
          zIndex: 1,
          color: "#fff",
          width: 50,
          height: 50,
          display: "flex",
          borderRadius: "50%",
          justifyContent: "center",
          alignItems: "center"
        },
        active: {
          backgroundImage: `linear-gradient( 136deg, ${color1} 0%, ${color2} 50%, ${color3} 100%)`,
          boxShadow: "0 4px 10px 0 rgba(0,0,0,.25)"
        },
        completed: {
          backgroundImage: `linear-gradient( 136deg, ${color1} 0%, ${color2} 50%, ${color3} 100%)`
        }
      })();
      const { active, completed } = props;

      return (
        <div
          className={clsx(classes.root, {
            [classes.active]: active,
            [classes.completed]: completed
          })}
        >
          {icons[String(props.icon)]}
        </div>
      );
    },
    [color1, color2, color3, icons]
  );

  useEffect(() => {
    setActiveStep(currentStep);
  }, [currentStep]);
  return (
    <div className={classes.root}>
      <Stepper
        alternativeLabel
        activeStep={activeStep}
        connector={<ColorLibConnector />}
      >
        {steps.map(step => (
          <Step key={step.label}>
            <StepLabel StepIconComponent={ColorLibStepIcon}>
              {step.label}
            </StepLabel>
          </Step>
        ))}
      </Stepper>
    </div>
  );
}
