import {Button, Grid, makeStyles} from "@material-ui/core"
import React, {useCallback} from "react"
import useForm from "../../hooks/useForm"
import FormItemResolverComponent from "./resolver"

export interface FormItemBase {
    label: string
    id: string
    autoComplete?: string
    alignment: {xs?: 12 | 6; sm?: 12 | 6}
    required?: boolean
    defaultValue?: any
    hint?: string
}

export interface TextFormItem extends FormItemBase {
    textType?: string
    type: "text"
}

export interface LocationFormItem extends FormItemBase {
    textType?: string
    freeSolo? : boolean
    locationIds?: {
        latitude: string
        longitude: string
    }
    type: "location"
}

export interface SliderFormItem extends FormItemBase {
    ending?: string
    step: number
    type: "slider"
    min: number
    max: number
}

export interface SelectFormItem extends FormItemBase {
    options: {[value: string]: string}
    type: "select"
}

export interface FileUploadFormItem extends FormItemBase {
    fileNameId?: string
    type: "file-upload"
}

export type FormItem =
    | TextFormItem
    | SelectFormItem
    | SliderFormItem
    | LocationFormItem
    | FileUploadFormItem

const useStyles = makeStyles(theme => ({
    form: {
        width: "100%", // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    spacing: {
        marginTop: 30,
    },
}))

export interface FormComponentProps<T> {
    formItems: FormItem[]
    initialForm?: Partial<T>
    onSubmit: (form: T) => void
    onUpdate?: (id: string, value: any) => void
    submitButtonTitle: string
    children?: JSX.Element | JSX.Element[]
}
export default function FormComponent<T>({
    formItems,
    submitButtonTitle,
    onSubmit,
    children,
    initialForm,
    onUpdate,
}: FormComponentProps<T>) {
    const classes = useStyles()
    const {form, updateForm} = useForm<T>(initialForm)

    const update = useCallback(
        (id: string, value: any) => {
            updateForm(id, value)
            if (onUpdate) {
                onUpdate(id, value)
            }
        },
        [updateForm, onUpdate],
    )

    return (
        <form
            className={classes.form}
            onSubmit={event => {
                event.preventDefault()
                onSubmit(form as T)
            }}>
            <Grid container spacing={2}>
                {formItems.map((formItem, i) => {
                    const {xs, sm} = formItem.alignment
                    return (
                        <Grid item xs={xs} sm={sm} key={i}>
                            <FormItemResolverComponent
                                handleChange={update}
                                {...formItem}
                            />
                        </Grid>
                    )
                })}
            </Grid>
            {children}
            <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.spacing}>
                {submitButtonTitle}
            </Button>
        </form>
    )
}
