import React from "react"
import FormFileUploadItem from "./file-upload"
import {FormItem} from "./form"
import FormLocationItem from "./location"
import FormSelectItem from "./select"
import FormSliderItem from "./slider"
import FormTextItem from "./text"

export type FormItemResolverComponentProps = FormItem & {
    handleChange: (id: string, value: any) => void
}
export default function FormItemResolverComponent(
    formItem: FormItemResolverComponentProps,
) {
    const resolve = () => {
        switch (formItem.type) {
            case "text":
                return <FormTextItem {...formItem} />
            case "select":
                return <FormSelectItem {...formItem} />
            case "slider":
                return <FormSliderItem {...formItem} />

            case "location":
                return <FormLocationItem {...formItem} />

            case "file-upload":
                return <FormFileUploadItem {...formItem} />

            default:
                throw new Error("impossible case")
        }
    }

    return <div>{resolve()}</div>
}
