import Paper from "@material-ui/core/Paper"
import {
    createStyles,
    lighten,
    makeStyles,
    Theme,
} from "@material-ui/core/styles"
import Table from "@material-ui/core/Table"
import TableBody from "@material-ui/core/TableBody"
import TableCell from "@material-ui/core/TableCell"
import TableContainer from "@material-ui/core/TableContainer"
import TableHead from "@material-ui/core/TableHead"
import TablePagination from "@material-ui/core/TablePagination"
import TableRow from "@material-ui/core/TableRow"
import TableSortLabel from "@material-ui/core/TableSortLabel"
import Toolbar from "@material-ui/core/Toolbar"
import Typography from "@material-ui/core/Typography"
import clsx from "clsx"
import React from "react"

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            marginBottom: theme.spacing(2),
            width: "100%",
        },
        root: {
            width: "100%",
        },
        table: {
            minWidth: 750,
        },
        visuallyHidden: {
            border: 0,
            clip: "rect(0 0 0 0)",
            height: 1,
            margin: -1,
            overflow: "hidden",
            padding: 0,
            position: "absolute",
            top: 20,
            width: 1,
        },
    }),
)

function descendingComparator<T>(a: T, b: T, orderBy: keyof T) {
    if (b[orderBy] < a[orderBy]) {
        return -1
    }
    if (b[orderBy] > a[orderBy]) {
        return 1
    }
    return 0
}

type Order = "asc" | "desc"

const getComparator = <T extends object>(
    order: Order,
    orderBy: keyof T,
): ((a: T, b: T) => number) =>
    order === "desc"
        ? (a, b) => descendingComparator(a, b, orderBy)
        : (a, b) => -descendingComparator(a, b, orderBy)

function stableSort<T>(array: T[], comparator: (a: T, b: T) => number) {
    const stabilizedThis = array.map((el, index) => [el, index] as [T, number])
    stabilizedThis.sort((a, b) => {
        const order = comparator(a[0], b[0])
        if (order !== 0) return order
        return a[1] - b[1]
    })
    return stabilizedThis.map(el => el[0])
}

export interface HeadCell<T> {
    disablePadding: boolean
    id: keyof T
    label: string
    numeric: boolean
}

interface EnhancedTableHeadProps<T> {
    classes: ReturnType<typeof useStyles>
    headCells: HeadCell<T>[]
    onRequestSort: (event: React.MouseEvent<unknown>, property: keyof T) => void
    order: Order
    orderBy: keyof T
}

function EnhancedTableHead<T>(props: EnhancedTableHeadProps<T>) {
    const {classes, headCells, onRequestSort, order, orderBy} = props
    const createSortHandler = (property: keyof T) => (
        event: React.MouseEvent<unknown>,
    ) => {
        onRequestSort(event, property)
    }

    return (
        <TableHead>
            <TableRow>
                <TableCell padding="checkbox" />
                {headCells.map(headCell => (
                    <TableCell
                        key={String(headCell.id)}
                        align={headCell.numeric ? "right" : "left"}
                        padding={headCell.disablePadding ? "none" : "default"}
                        sortDirection={orderBy === headCell.id ? order : false}>
                        <TableSortLabel
                            active={orderBy === headCell.id}
                            direction={orderBy === headCell.id ? order : "asc"}
                            onClick={createSortHandler(headCell.id)}>
                            {headCell.label}
                            {orderBy === headCell.id ? (
                                <span className={classes.visuallyHidden}>
                                    {order === "desc"
                                        ? "sorted descending"
                                        : "sorted ascending"}
                                </span>
                            ) : null}
                        </TableSortLabel>
                    </TableCell>
                ))}
            </TableRow>
        </TableHead>
    )
}

const useToolbarStyles = makeStyles((theme: Theme) =>
    createStyles({
        highlight:
            theme.palette.type === "light"
                ? {
                      backgroundColor: lighten(
                          theme.palette.secondary.light,
                          0.85,
                      ),
                      color: theme.palette.secondary.main,
                  }
                : {
                      backgroundColor: theme.palette.secondary.dark,
                      color: theme.palette.text.primary,
                  },
        root: {
            paddingLeft: theme.spacing(2),
            paddingRight: theme.spacing(1),
        },
        title: {
            flex: "1 1 100%",
        },
    }),
)

interface EnhancedTableToolbarProps {
    title: string
    toolbarBarItems?: JSX.Element[]
}

const EnhancedTableToolbar = ({
    title,
    toolbarBarItems = [],
}: EnhancedTableToolbarProps) => {
    const classes = useToolbarStyles()

    return (
        <Toolbar className={clsx(classes.root)}>
            <Typography className={classes.title} variant="h6" id="tableTitle">
                {title}
            </Typography>
            {toolbarBarItems}
        </Toolbar>
    )
}

export interface EnhancedTableProps<T> {
    headCells: HeadCell<T>[]
    initialOrderProperty: keyof T
    nameProperty: keyof T
    onRowClick: (row: T) => void
    rows: T[]
    title: string

    toolbarBarItems?: JSX.Element[]
}
export type ComparableObject = {[key: string]: number | string | Date}
export default function EnhancedTable<T extends ComparableObject>({
    initialOrderProperty,
    nameProperty,
    rows,
    headCells,
    title,
    onRowClick,
    toolbarBarItems = [],
}: EnhancedTableProps<T>) {
    const classes = useStyles()
    const [order, setOrder] = React.useState<Order>("desc")
    const [orderBy, setOrderBy] = React.useState<keyof T>(initialOrderProperty)
    const [page, setPage] = React.useState(0)
    const [rowsPerPage, setRowsPerPage] = React.useState(20)

    const handleRequestSort = (property: keyof T) => {
        const isAsc = orderBy === property && order === "asc"
        setOrder(isAsc ? "desc" : "asc")
        setOrderBy(property)
    }

    const handleChangePage = (newPage: number) => {
        setPage(newPage)
    }

    const handleChangeRowsPerPage = (
        event: React.ChangeEvent<HTMLInputElement>,
    ) => {
        setRowsPerPage(parseInt(event.target.value, 10))
        setPage(0)
    }

    const emptyRows =
        rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage)

    return (
        <div className={classes.root}>
            <Paper className={classes.paper}>
                <EnhancedTableToolbar
                    title={title}
                    toolbarBarItems={toolbarBarItems}
                />
                <TableContainer>
                    <Table
                        className={classes.table}
                        aria-labelledby="tableTitle"
                        size="medium"
                        aria-label="enhanced table">
                        <EnhancedTableHead
                            headCells={headCells}
                            classes={classes}
                            order={order}
                            orderBy={orderBy}
                            onRequestSort={(_, property) =>
                                handleRequestSort(property)
                            }
                        />
                        <TableBody>
                            {stableSort(rows, getComparator<T>(order, orderBy))
                                .slice(
                                    page * rowsPerPage,
                                    page * rowsPerPage + rowsPerPage,
                                )
                                .map(row => {
                                    return (
                                        <TableRow
                                            hover
                                            onClick={() => onRowClick(row)}
                                            tabIndex={-1}
                                            key={String(
                                                row.id || row[nameProperty],
                                            )}>
                                            <TableCell padding="checkbox" />
                                            <TableCell
                                                component="th"
                                                scope="row"
                                                padding="none">
                                                {row[nameProperty]}
                                            </TableCell>
                                            {headCells
                                                .filter(property => {
                                                    return (
                                                        property.id !==
                                                        nameProperty
                                                    )
                                                })
                                                .map(rowName => {
                                                    return (
                                                        <TableCell
                                                            key={rowName.id.toString()}
                                                            align="right">
                                                            {row[rowName.id]}
                                                        </TableCell>
                                                    )
                                                })}
                                        </TableRow>
                                    )
                                })}
                            {emptyRows > 0 && (
                                <TableRow style={{height: 53 * emptyRows}}>
                                    <TableCell colSpan={6} />
                                </TableRow>
                            )}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[5, 10, 20, 30]}
                    component="div"
                    count={rows.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={(_, page) => handleChangePage(page)}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>
        </div>
    )
}
