import {ApolloProvider} from "@apollo/react-hooks"
import {Snackbar} from "@material-ui/core"
import MuiAlert, {AlertProps} from "@material-ui/lab/Alert"
import React, {useState} from "react"
import {BrowserRouter as Router, Route, Switch} from "react-router-dom"
import {client, NotifyMessage} from "./client"
import IndexPage from "./pages"
import AboutPage from "./pages/about"
import AccountChoosePage from "./pages/account/choose"
import ForgotPasswordPage from "./pages/account/forgot-password"
import {LoginPage} from "./pages/account/login"
import {LogoutPage} from "./pages/account/logout"
import PasswordResetPage from "./pages/account/password-reset"
import RegisterPage from "./pages/account/register"
import AccountUpdatePage from "./pages/account/update"
import ContactPage from "./pages/contact"
import FindPage from "./pages/find"
import JobPage from "./pages/job"
import JobCreatePage from "./pages/job/create"
import LegalPage from "./pages/legal"
import DisclaimerPage from "./pages/legal/disclaimer"
import FaqPage from "./pages/legal/faq"
import PrivacyPolicyPage from "./pages/legal/privacy-policy"
import TermsOfServicePage from "./pages/legal/terms-of-service"

function Alert(props: AlertProps) {
    return <MuiAlert elevation={6} variant="filled" {...props} />
}
export interface BasePageProps {
    updateSnack: (snack: NotifyMessage | undefined) => void
}

export interface AppViewProps {}

export const AppView: React.FC<AppViewProps> = () => {
    const [snack, updateSnack] = useState<NotifyMessage>()

    return (
        <div className="App">
            <Router>
                <Switch>
                    <Route path="/legal/terms-of-service">
                        <TermsOfServicePage />
                    </Route>

                    <Route path="/legal/privacy-policy">
                        <PrivacyPolicyPage />
                    </Route>

                    <Route path="/faq">
                        <FaqPage />
                    </Route>

                    <Route path="/legal/disclaimer">
                        <DisclaimerPage />
                    </Route>

                    <Route path="/account/logout">
                        <LogoutPage />
                    </Route>

                    <Route path="/account/password-reset">
                        <PasswordResetPage updateSnack={updateSnack} />
                    </Route>

                    <Route path="/account/forgot-password">
                        <ForgotPasswordPage updateSnack={updateSnack} />
                    </Route>

                    <Route path="/account/update">
                        <AccountUpdatePage updateSnack={updateSnack} />
                    </Route>

                    <Route path="/account/register/choose">
                        <AccountChoosePage />
                    </Route>

                    <Route path="/find">
                        <FindPage updateSnack={updateSnack} />
                    </Route>

                    <Route path="/job/create">
                        <JobCreatePage updateSnack={updateSnack} />
                    </Route>

                    <Route path="/job">
                        <JobPage updateSnack={updateSnack} />
                    </Route>

                    <Route path="/account/register">
                        <RegisterPage updateSnack={updateSnack} />
                    </Route>

                    <Route path="/account/login">
                        <LoginPage updateSnack={updateSnack} />
                    </Route>

                    <Route path="/about">
                        <AboutPage />
                    </Route>

                    <Route path="/legal">
                        <LegalPage />
                    </Route>

                    <Route path="/contact">
                        <ContactPage />
                    </Route>

                    <Route path="/">
                        <IndexPage updateSnack={updateSnack} />
                    </Route>
                </Switch>
            </Router>

            <Snackbar
                open={!!snack}
                autoHideDuration={6000}
                onClose={event => {
                    if (!event) {
                        updateSnack(undefined)
                    }
                }}>
                <Alert severity={snack?.severity || "error"}>
                    {snack ? snack.message.replace("GraphQL error:", "") : ""}
                </Alert>
            </Snackbar>
        </div>
    )
}

function App() {
    return (
        <ApolloProvider
            {...{
                client,
            }}>
            <AppView />
        </ApolloProvider>
    )
}

export default App
