import {Grow, makeStyles, Paper} from "@material-ui/core"
import * as React from "react"
import {useEffect, useState} from "react"
// @ts-ignore
import Markdown from "react-remarkable"

const useStyles = makeStyles(() => ({
    container: {
        display: "flex",
        justifyContent: "center",
    },

    paper: {
        marginTop: "1vh",
        maxWidth: 800,
        width: "90vw",
    },
}))

export interface MarkdownComponentProps {
    url: string
    variableMaps?: {from: string | RegExp; to: string}[]
    onFinish?: () => void
}

const MarkdownComponent = ({
    url: fileName,
    onFinish,
    variableMaps = [],
}: MarkdownComponentProps) => {
    const classes = useStyles()

    const [content, changeContent] = useState<string>()

    useEffect(() => {
        ;(async () => {
            let response = await fetch(`/assets/docs/${fileName}`)
            let markdown = await response.text()
            variableMaps.forEach(({from, to}) => {
                markdown = markdown.replace(from, to)
            })
            changeContent(markdown)
        })()
    })

    return (
        <div className={classes.container}>
            <Grow onEntered={onFinish} timeout={1000} in={true}>
                <Paper className={classes.paper}>
                    <div style={{padding: 25}}>
                        {content ? (
                            <Markdown options={{html: true}} source={content} />
                        ) : null}
                    </div>
                </Paper>
            </Grow>
        </div>
    )
}

export default MarkdownComponent
