export type AllUserInfoFragment = (
  { __typename: 'User' }
  & Pick<User, 'id' | 'type' | 'email' | 'address' | 'latitude' | 'longitude' | 'firstName' | 'lastName'>
);

export type BasicUserInfoFragment = (
  { __typename: 'User' }
  & Pick<User, 'id' | 'type' | 'email'>
);

export type MeUserInfoFragment = (
  { __typename: 'CurrentUser' }
  & Pick<CurrentUser, 'id'>
  & { user: (
    { __typename: 'User' }
    & AllUserInfoFragment
  ) }
);

export type AuthenticationResultInfoFragment = (
  { __typename: 'AuthenticationResult' }
  & Pick<AuthenticationResult, 'token'>
);

export type JobInfoFragment = (
  { __typename: 'Job' }
  & Pick<Job, 'id' | 'name' | 'description' | 'address' | 'progress' | 'quantity' | 'createdAt' | 'trackingNumber'>
  & { file: (
    { __typename: 'File' }
    & Pick<File, 'link' | 'id'>
  ), submitter: (
    { __typename: 'User' }
    & BasicUserInfoFragment
  ), volunteer?: Maybe<(
    { __typename: 'User' }
    & BasicUserInfoFragment
  )> }
);

