export const AllUserInfoFragmentDoc = gql`
    fragment AllUserInfo on User {
  id
  type
  email
  address
  latitude
  longitude
  firstName
  lastName
}
    `;
export const MeUserInfoFragmentDoc = gql`
    fragment MeUserInfo on CurrentUser {
  id
  user {
    ...AllUserInfo
  }
}
    ${AllUserInfoFragmentDoc}`;
export const AuthenticationResultInfoFragmentDoc = gql`
    fragment AuthenticationResultInfo on AuthenticationResult {
  token
}
    `;
export const BasicUserInfoFragmentDoc = gql`
    fragment BasicUserInfo on User {
  id
  type
  email
}
    `;
export const JobInfoFragmentDoc = gql`
    fragment JobInfo on Job {
  id
  name
  file {
    link
    id
  }
  description
  address
  progress
  quantity
  createdAt
  trackingNumber
  submitter {
    ...BasicUserInfo
  }
  volunteer {
    ...BasicUserInfo
  }
}
    ${BasicUserInfoFragmentDoc}`;
