//@ts-ignore
import {Helper} from "dxf"
import React, {useEffect, useState} from "react"

export interface DXFViewerProps {
    source: {url: string; type: "url"} | {data: string; type: "data"}
    strokeWidth?: string
    strokeColor?: string
    width: number
    height: number
}
const DXFViewer: React.FC<DXFViewerProps> = ({
    source,
    strokeWidth,
    strokeColor,
    width,
    height,
}) => {
    const [base64, changeSBase64] = useState<string>()

    useEffect(() => {
        const updateSvg = (dxf: string) => {
            try {
                // eslint-disable-next-line no-control-regex
                const isAscii = /^[\x00-\x7F]*$/.test(dxf)
                if (isAscii) {
                    const helper = new Helper(dxf)
                    const svg = helper
                        .toSVG()
                        .replace(
                            `stroke-width="0.1%"`,
                            `stroke-width="${strokeWidth || "0.1%"}"`,
                        )
                        .replace(
                            `stroke="rgb(0, 0, 0)"`,
                            `stroke="${strokeColor || "rgb(0, 0, 0)"}"`,
                        )

                        .replace(
                            `stroke="#000000"`,
                            `stroke="${strokeColor || "#000000"}"`,
                        )

                    const buff = new Buffer(svg)
                    const base64data = buff.toString("base64")
                    changeSBase64(base64data)
                }
            } catch {}
        }

        const getDXFData = async () => {
            if (source.type === "data") {
                updateSvg(source.data)
            } else {
                console.log(source.url)
                const response = await fetch(source.url)
                const dxf = await response.text()
                updateSvg(dxf)
            }
        }
        getDXFData()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <div>
            {base64 ? (
                <img
                    style={{width, height}}
                    src={`data:image/svg+xml;base64,${base64}`}
                    alt=""
                />
            ) : null}
        </div>
    )
}

export default DXFViewer
