import React, {useCallback, useEffect} from "react"
import {LocationFeature} from "../../services/location-search"
import OsmAutocomplete from "../osm-autocomplete"
import {LocationFormItem} from "./form"

export type FormLocationItemProps = LocationFormItem & {
    handleChange: (id: string, value: string | number) => void
}
const FormLocationItem = ({
    id,
    autoComplete,
    label,
    required,
    handleChange,
    defaultValue,
    locationIds,
    freeSolo = false
}: FormLocationItemProps) => {
    const setLocation = useCallback(
        (location: LocationFeature | undefined | null) => {
            if (location) {
                handleChange(id, location.properties.display_name)
                if (locationIds) {
                    const {latitude, longitude} = locationIds
                    if (location.geometry){
                        handleChange(latitude, location.geometry.coordinates[1])
                        handleChange(longitude, location.geometry.coordinates[0])
                    }
                }
            }
        },
        [handleChange, id, locationIds],
    )

    useEffect(() => {
        setLocation(defaultValue)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <OsmAutocomplete
            textProps={{
                variant: "outlined",
                required,
                fullWidth: true,
                id,
                label,
                name: id,
                autoComplete: autoComplete,
            }}
            freeSolo={freeSolo}
            defaultValue={defaultValue}
            onLocationSelect={feature => {
                setLocation(feature)
            }}
        />
    )
}

export default FormLocationItem
