import {
    Button,
    ButtonProps,
    createStyles,
    Fade,
    makeStyles,
} from "@material-ui/core"
import AppBar from "@material-ui/core/AppBar"
import CssBaseline from "@material-ui/core/CssBaseline"
import Link from "@material-ui/core/Link"
import Toolbar from "@material-ui/core/Toolbar"
import useScrollTrigger from "@material-ui/core/useScrollTrigger"
import PropTypes from "prop-types"
import React, {useEffect, useState} from "react"
import theme from "./theme"

const useStyles = makeStyles(() =>
    createStyles({
        sidebarButtons: {
            display: "flex",
            justifyContent: "flex-start",
        },
        link: {
            textDecoration: "none",
        },
    }),
)

export interface SideBarButton {
    title: string
    path: string
    props?: ButtonProps
    type: "bar-button"
}

export interface AppBarProps {
    window?: () => Window
    homeButton: SideBarButton
    buttons: (SideBarButton | {element: JSX.Element; type: "element"})[]
    inverted: boolean
    disableHide: boolean
}

let previousScroll = 0

const HideOnScroll = (
    props: React.PropsWithChildren<AppBarProps> & {
        throttleInverted: (inverted: boolean) => void
    },
) => {
    const {children, window: window_prop, throttleInverted} = props
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.

    const threshold = 50
    const trigger = useScrollTrigger({
        target: window_prop ? window_prop() : undefined,
        threshold,
    })
    useEffect(() => {
        window.addEventListener("scroll", () => {
            if (window.scrollY <= 0) {
                throttleInverted(false)
            } else if (
                window.scrollY < previousScroll &&
                !trigger &&
                window.scrollY > threshold
            ) {
                throttleInverted(true)
            }
            previousScroll = window.scrollY
        })
    }, [throttleInverted, trigger])

    return <Fade in={!trigger}>{children}</Fade>
}

HideOnScroll.propTypes = {
    children: PropTypes.node.isRequired,
    // Injected by the documentation to work in an iframe.
    // You won't need it on your project.
    window: PropTypes.func,
}

const HideAppBar: React.FunctionComponent<AppBarProps> = props => {
    const classes = useStyles()
    const {
        children,
        buttons,
        homeButton,
        inverted: invertedInitial,
        disableHide,
    } = props
    const [inverted, changeInverted] = useState(invertedInitial)
    const buttonStyle = {
        fontFamily: theme.typography.fontFamily,
        fontSize: 15,
        color: inverted ? "white" : theme.palette.primary.main,
        textTransform: "none" as "none",
    }

    const appBar = (
        <AppBar
            elevation={0}
            style={{
                background: inverted ? theme.palette.primary.main : "white",
            }}>
            <Toolbar className={classes.sidebarButtons}>
                <div style={{flexGrow: 1}}>
                    <Link href={homeButton.path}>
                        <Button style={buttonStyle} color="inherit">
                            {homeButton.title}
                        </Button>
                    </Link>
                </div>
                <div>
                    {buttons.map((button, i) => {
                        if (button.type === "element") {
                            return <Link key={i}>{button.element}</Link>
                        } else {
                            const {path, title, props} = button
                            return (
                                <Link key={i} href={path}>
                                    <Button
                                        style={buttonStyle}
                                        color="inherit"
                                        {...props}>
                                        {title}
                                    </Button>
                                </Link>
                            )
                        }
                    })}
                </div>
            </Toolbar>
        </AppBar>
    )

    return (
        <React.Fragment>
            <CssBaseline />
            {disableHide ? (
                <div>{appBar}</div>
            ) : (
                <HideOnScroll throttleInverted={changeInverted} {...props}>
                    {appBar}
                </HideOnScroll>
            )}

            <Toolbar />
            {children}
        </React.Fragment>
    )
}

export default HideAppBar
