import {Button} from "@material-ui/core"
import * as React from "react"
import {useEffect, useState} from "react"
import {BasePageProps} from "../App"
import PageComponent from "../components/page"
import EnhancedTable, {ComparableObject, HeadCell} from "../components/table"
import {
    AllUserInfoFragment,
    JobInfoFragment,
    useAcceptedJobsQuery,
    UserType,
    useSubmittedJobsQuery,
} from "../graphql/schema.generated"
import {goTo} from "../services/utils"
import HomePage from "./home"

const headCells: HeadCell<JobInfoFragment>[] = [
    {
        id: "name",
        numeric: false,
        disablePadding: true,
        label: "Print Jobs",
    },
    {id: "id", numeric: true, disablePadding: false, label: "Job ID"},
    {id: "progress", numeric: true, disablePadding: false, label: "Progress"},
    {id: "quantity", numeric: true, disablePadding: true, label: "Quantity"},
    {id: "createdAt", numeric: true, disablePadding: false, label: "Created"},
]

export interface AuthenticatedHomePageViewProps {
    jobs: JobInfoFragment[]
    type: UserType
}
export const AuthenticatedHomePageView: React.FC<AuthenticatedHomePageViewProps> = ({
    jobs,
    type,
}) => {
    return (
        <EnhancedTable
        initialOrderProperty={"createdAt"}
        nameProperty={"name"}
            toolbarBarItems={[
                type === "Submitter" ? (
                    <Button
                        key={"create"}
                        href="/job/create"
                        style={{width: 300, height : 40}}
                        variant="contained"
                        color="primary">
                        Create Print Job
                    </Button>
                ) : (
                    <Button
                        key={"find"}
                        href="/find"
                        style={{width: 300, height : 40}}
                        variant="contained"
                        color="primary">
                        Find Print Jobs
                    </Button>
                ),
            ]}
            rows={(jobs as unknown) as ComparableObject[]}
            headCells={(headCells as unknown) as HeadCell<ComparableObject>[]}
            onRowClick={row => {
                goTo(`/job?jobID=${row.id}`)
            }}
            title={type === "Submitter" ? "Submitted Jobs" : "Accepted Jobs"}
        />
    )
}

interface SubmitterHomePageProps extends BasePageProps {
    type: UserType
}
const SubmitterHomePage: React.FC<SubmitterHomePageProps> = ({
    type,
    updateSnack,
}) => {
    const {error, data} = useSubmittedJobsQuery()
    const [jobs, changeJobs] = useState<JobInfoFragment[]>()
    useEffect(() => {
        if (!error && data?.submittedJobs) {
            changeJobs(data.submittedJobs)
        } else if (error) {
            updateSnack({message: error.message})
        }
    }, [data, error, updateSnack])

    return (
        <div>
            {jobs ? (
                <AuthenticatedHomePageView jobs={jobs} type={type} />
            ) : null}
        </div>
    )
}

interface VolunteerHomePageProps extends BasePageProps {
    type: UserType
}
const VolunteerHomePage: React.FC<VolunteerHomePageProps> = ({
    type,
    updateSnack,
}) => {
    const {error, data} = useAcceptedJobsQuery()
    const [jobs, changeJobs] = useState<JobInfoFragment[]>()
    useEffect(() => {
        if (!error && data?.acceptedJobs) {
            changeJobs(data.acceptedJobs)
        } else if (error) {
            updateSnack({message: error.message})
        }
    }, [data, error, updateSnack])

    return (
        <div>
            {jobs ? (
                <AuthenticatedHomePageView jobs={jobs} type={type} />
            ) : null}
        </div>
    )
}

export interface IndexPageProps extends BasePageProps {}
const IndexPage: React.FC<IndexPageProps> = props => {
    return (
        <div>
            <PageComponent
                disableHide={false}
                showFooter={true}
                title="Home"
                rolePages={[
                    {
                        component: (user: AllUserInfoFragment) => (
                            <SubmitterHomePage type={user.type} {...props} />
                        ),
                        role: "Submitter",
                    },
                    {
                        component: (user: AllUserInfoFragment) => (
                            <VolunteerHomePage type={user.type} {...props} />
                        ),
                        role: "Volunteer",
                    },
                ]}>
                <HomePage />
            </PageComponent>
            <br />
        </div>
    )
}

export default IndexPage
