import {FormControl, InputLabel, OutlinedInput} from "@material-ui/core"
import HelpIcon from "@material-ui/icons/Help"
import React, {useEffect, useState} from "react"
import TooltipComponent from "../tooltip"
import {TextFormItem} from "./form"

export type FormTextItemProps = TextFormItem & {
    handleChange: (id: string, value: string) => void
}
const FormTextItem = ({
    id,
    autoComplete,
    textType,
    label,
    required,
    handleChange,
    defaultValue,
    hint,
}: FormTextItemProps) => {
    const [textValue, changeTextValue] = useState<string>(defaultValue || "")

    useEffect(() => {
        handleChange(id, defaultValue)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <div style={{display: "flex"}}>
            <FormControl fullWidth variant="outlined">
                <InputLabel htmlFor={id}>{label}</InputLabel>
                <OutlinedInput
                    fullWidth
                    type={textType}
                    required={required}
                    value={textValue}
                    onChange={({target: {name, value}}) => {
                        changeTextValue(value)
                        handleChange(name, value)
                    }}
                    id={id}
                    name={id}
                    label={label}
                    autoComplete={autoComplete}
                    endAdornment={
                        hint ? (
                            <TooltipComponent hint={hint} icon={<HelpIcon />} />
                        ) : (
                            undefined
                        )
                    }
                />
            </FormControl>
        </div>
    )
}

export default FormTextItem
