import {Typography} from "@material-ui/core"
import Avatar from "@material-ui/core/Avatar"
import Container from "@material-ui/core/Container"
import Grid from "@material-ui/core/Grid"
import Link from "@material-ui/core/Link"
import {makeStyles} from "@material-ui/core/styles"
import LockOutlinedIcon from "@material-ui/icons/LockOutlined"
import React, {useCallback, useMemo} from "react"
import {BasePageProps} from "../../App"
import {tokenKey} from "../../client"
import FormComponent, {FormItem} from "../../components/form/form"
import PageComponent from "../../components/page"
import {
    MeDocument,
    MutationLoginArgs,
    useLoginMutation,
} from "../../graphql/schema.generated"
import {goTo} from "../../services/utils"

const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
}))

export interface LoginPageViewProps {
    onSubmit: (form: MutationLoginArgs) => void
}

export const LoginPageView: React.FC<LoginPageViewProps> = ({onSubmit}) => {
    const classes = useStyles()

    const formItems = useMemo(
        () =>
            [
                {
                    type: "text",
                    required: true,
                    alignment: {xs: 12},
                    id: ".email",
                    label: "Email",
                    autoComplete: "email",
                },
                {
                    type: "text",
                    required: true,
                    textType: "password",
                    alignment: {xs: 12},
                    id: ".password",
                    label: "Password",
                    autoComplete: "password",
                },
            ] as FormItem[],
        [],
    )

    return (
        <Container component="main" maxWidth="xs">
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Login
                </Typography>

                <FormComponent
                    submitButtonTitle="Login"
                    formItems={formItems}
                    onSubmit={onSubmit}>
                    <Grid style={{marginTop: 10}} container justify="flex-end">
                        <Grid item>
                            <Link href="/account/register" variant="body2">
                                Don't have an account?
                            </Link>
                        </Grid>
                    </Grid>

                    <Grid container justify="flex-end">
                        <Grid item>
                            <Link
                                href="/account/forgot-password"
                                variant="body2">
                                Forgot Password?
                            </Link>
                        </Grid>
                    </Grid>
                </FormComponent>
            </div>
        </Container>
    )
}

export interface LoginPageProps extends BasePageProps {}
export const LoginPage: React.FC<LoginPageProps> = ({updateSnack}) => {
    const [mutate] = useLoginMutation({
        onCompleted: data => {
            if (data.login) {
                localStorage.setItem(tokenKey, data.login.token)
                goTo("/")
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
        refetchQueries: [{query: MeDocument}],
    })

    const onSubmit = useCallback(
        (args: MutationLoginArgs) => {
            mutate({
                variables: args,
            })
        },
        [mutate],
    )

    return (
        <PageComponent
            showFooter={true}
            title="Login"
            rolePages={[
                {role: "Submitter", redirect: "/"},
                {role: "Volunteer", redirect: "/"},
            ]}>
            <LoginPageView onSubmit={onSubmit} />
        </PageComponent>
    )
}
