import {makeStyles, Slider, Typography} from "@material-ui/core"
import React, {useEffect} from "react"
import {SliderFormItem} from "./form"

const useStyles = makeStyles({
    root: {
        flex: 1,
        width: 390,
    },
})

function valuetext(value: number, ending: string) {
    return `${value} ${ending}`
}

export type FormSliderItemProps = SliderFormItem & {
    handleChange: (id: string, value: number) => void
}

const FormSliderItem = ({
    id,
    step,
    ending = "",
    defaultValue = 0,
    min,
    max,
    label,
    handleChange,
}: FormSliderItemProps) => {
    const classes = useStyles()
    useEffect(() => {
        handleChange(id, defaultValue)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    return (
        <div className={classes.root}>
            <Typography id="discrete-slider" gutterBottom>
                {label}
            </Typography>
            <Slider
                id={id}
                onChange={(_, value) => {
                    if (typeof value === "number") {
                        handleChange(id, value)
                    }
                }}
                defaultValue={defaultValue}
                getAriaValueText={value => valuetext(value, ending)}
                aria-labelledby="discrete-slider-always"
                step={step}
                valueLabelDisplay="on"
                min={min}
                max={max}
            />
        </div>
    )
}

export default FormSliderItem
