import * as React from "react"
import {useEffect, useState} from "react"
import {BasePageProps} from "../App"
import PageComponent from "../components/page"
import {RenderType} from "../components/part-renderer"
import EnhancedTable, {ComparableObject, HeadCell} from "../components/table"
import WrongPageComponent from "../components/wrong-page"
import {
    JobInfoFragment,
    useAvailableJobsQuery,
} from "../graphql/schema.generated"
import {goTo, renderFileType} from "../services/utils"

const headCells: HeadCell<JobInfoFragment & {jobType: RenderType}>[] = [
    {
        disablePadding: true,
        id: "name",
        label: "Jobs",
        numeric: false,
    },
    {disablePadding: false, id: "id", label: "Job ID", numeric: true},
    {disablePadding: false, id: "address", label: "Address", numeric: true},
    {disablePadding: false, id: "quantity", label: "Quantity", numeric: true},
    {disablePadding: false, id: "createdAt", label: "Created", numeric: true},
    {disablePadding: false, id: "jobType", label: "Type", numeric: true},
]

export interface FindPageViewProps {
    availableJobs: JobInfoFragment[]
    loading: boolean
}
export const FindPageView: React.FC<FindPageViewProps> = ({
    availableJobs: acceptedJobs,
}) => (
    <PageComponent
        showFooter
        title="Find Jobs"
        rolePages={[
            {
                component: () => (
                    <EnhancedTable
                        initialOrderProperty="nearness"
                        nameProperty="name"
                        rows={
                            (acceptedJobs.map((job, i) => {
                                return {
                                    ...job,
                                    jobType:
                                        renderFileType(job.name) === "dxf"
                                            ? "Laser Cut"
                                            : "3D Print",
                                    nearness: acceptedJobs.length - i,
                                }
                            }) as unknown) as ComparableObject[]
                        }
                        headCells={
                            (headCells as unknown) as HeadCell<
                                ComparableObject
                            >[]
                        }
                        onRowClick={row => {
                            goTo(`/job?jobID=${row.id}`)
                        }}
                        title="Available Jobs"
                    />
                ),
                role: "Volunteer",
            },
        ]}>
        <WrongPageComponent text="Find only provided for volunteers" />
    </PageComponent>
)

export interface FindPageProps extends BasePageProps {}
const FindPage: React.FC<FindPageProps> = ({updateSnack}) => {
    const {data, error, loading} = useAvailableJobsQuery()
    const [availableJobs, changeAvailableJobs] = useState<JobInfoFragment[]>([])

    useEffect(() => {
        if (!error && data?.availableJobs) {
            changeAvailableJobs(data.availableJobs)
        } else if (error) {
            updateSnack({message: error.message})
        }
    }, [error, data, changeAvailableJobs, updateSnack])
    return <FindPageView availableJobs={availableJobs} loading={loading} />
}

export default FindPage