import * as React from "react";
import { useState } from "react";
import MarkdownComponent from "../../components/markdown";
import PageComponent from "../../components/page";



const FaqPage = () => {
  const [showFooter, changeShowFooter] = useState<boolean>();
  return (
    <PageComponent disableHide={false} showFooter={showFooter} title="FAQ">
      <MarkdownComponent
        onFinish={() => changeShowFooter(true)}
        url="faq.md"
      />
    </PageComponent>
  );
};

export default FaqPage;
