import {Typography} from "@material-ui/core"
import Avatar from "@material-ui/core/Avatar"
import Container from "@material-ui/core/Container"
import {makeStyles} from "@material-ui/core/styles"
import LockOutlinedIcon from "@material-ui/icons/LockOutlined"
import React, {useCallback, useMemo} from "react"
import {BasePageProps} from "../../App"
import FormComponent, {FormItem} from "../../components/form/form"
import PageComponent from "../../components/page"
import {
    MutationForgotPasswordArgs,
    useForgotPasswordMutation,
} from "../../graphql/schema.generated"
import {goTo} from "../../services/utils"

const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
}))

export interface ForgotPasswordPageViewProps {
    onSubmit: (form: MutationForgotPasswordArgs) => void
}

export const ForgotPasswordPageView: React.FC<ForgotPasswordPageViewProps> = ({
    onSubmit,
}) => {
    const classes = useStyles()

    const formItems = useMemo(
        () =>
            [
                {
                    type: "text",
                    required: true,
                    alignment: {xs: 12},
                    id: ".email",
                    label: "Email",
                    autoComplete: "email",
                },
            ] as FormItem[],
        [],
    )

    return (
        <Container component="main" maxWidth="xs">
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Forgot Password
                </Typography>

                <FormComponent
                    submitButtonTitle="Reset Password"
                    formItems={formItems}
                    onSubmit={onSubmit}></FormComponent>
            </div>
        </Container>
    )
}

export interface ForgotPasswordPageProps extends BasePageProps {}
const ForgotPasswordPage: React.FC<ForgotPasswordPageProps> = ({
    updateSnack,
}) => {
    const [mutate] = useForgotPasswordMutation({
        onCompleted: data => {
            if (data.forgotPassword) {
                updateSnack({
                    message: "successfully sent email. redirecting...",
                    severity: "success",
                })
                setTimeout(() => {
                    goTo("/account/login")
                }, 1000)
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
    })

    const onSubmit = useCallback(
        (args: MutationForgotPasswordArgs) => {
            mutate({
                variables: args,
            })
        },
        [mutate],
    )

    return (
        <PageComponent
            showFooter={true}
            title="Forgot Password"
            rolePages={[
                {role: "Submitter", redirect: "/"},
                {role: "Volunteer", redirect: "/"},
            ]}>
            <ForgotPasswordPageView onSubmit={onSubmit} />
        </PageComponent>
    )
}

export default ForgotPasswordPage
