import {Typography} from "@material-ui/core"
import Avatar from "@material-ui/core/Avatar"
import Container from "@material-ui/core/Container"
import CssBaseline from "@material-ui/core/CssBaseline"
import Grid from "@material-ui/core/Grid"
import Link from "@material-ui/core/Link"
import {makeStyles} from "@material-ui/core/styles"
import LockOutlinedIcon from "@material-ui/icons/LockOutlined"
import React, {useCallback, useMemo, useState} from "react"
import {BasePageProps} from "../../App"
import {tokenKey} from "../../client"
import FormComponent, {FormItem} from "../../components/form/form"
import PageComponent from "../../components/page"
import {
    MeDocument,
    MutationRegisterArgs,
    useRegisterMutation,
    UserType,
} from "../../graphql/schema.generated"
import {goTo, useQuery} from "../../services/utils"

const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
}))

export interface RegisterPageViewProps {
    onSubmit: (
        form: MutationRegisterArgs & {
            confirmPassword: string
        },
    ) => void
}
export const RegisterPageView: React.FC<RegisterPageViewProps> = ({
    onSubmit,
}) => {
    const classes = useStyles()
    const query = useQuery()
    const argUserType = useMemo(() => query.get("type"), [query])
    const registrationToken = query.get("token")
    const [userType, changeUserType] = useState<UserType>(
        argUserType &&
            (argUserType === "Submitter" || argUserType === "Volunteer")
            ? argUserType
            : "Volunteer",
    )
    const formItems = useMemo(
        () =>
            [
                {
                    type: "select",
                    options: {
                        Submitter: "Medical",
                        Volunteer: "Maker",
                    },
                    required: true,
                    alignment: {xs: 12},
                    id: ".type",
                    label: "User Type",
                    defaultValue: userType,
                },

                {
                    type: "text",
                    required: true,
                    alignment: {xs: 12},
                    id: ".registrationToken",
                    label: "Registration Token",
                    hint: "generated for you by admin",
                    defaultValue: registrationToken || "",
                },

                {
                    type: "text",
                    required: true,
                    alignment: {xs: 12},
                    id: ".email",
                    label:
                        userType === "Submitter"
                            ? "Email (public to volunteers)"
                            : "Email",
                    hint:
                        userType === "Submitter"
                            ? "this email can be viewed by makers to contact you"
                            : undefined,
                    autoComplete: "email",
                },
                {
                    type: "text",
                    textType: "password",
                    required: true,
                    alignment: {xs: 12},
                    id: ".password",
                    label: "Password",
                    autoComplete: "password",
                },

                {
                    type: "text",
                    textType: "password",
                    required: true,
                    alignment: {xs: 12},
                    id: ".confirmPassword",
                    label: "Confirm Password",
                    autoComplete: "password",
                },

                {
                    type: "text",
                    required: true,
                    alignment: {xs: 12, sm: 6},
                    id: ".firstName",
                    label: "First Name",
                    autoComplete: "fname",
                },

                {
                    type: "text",
                    required: true,
                    alignment: {xs: 12, sm: 6},
                    id: ".lastName",
                    label: "Last Name",
                    autoComplete: "lname",
                },

                ...(userType === "Submitter"
                    ? [
                          {
                              freeSolo: true,
                              type: "location",
                              required: true,
                              alignment: {xs: 12},
                              id: ".address",
                              label: "Shipping Address",
                              locationIds: {
                                  latitude: ".geolocation.latitude",
                                  longitude: ".geolocation.longitude",
                              },
                              hint:
                                  "address 3D printer owners should send parts",
                              autoComplete: "address",
                          },
                      ]
                    : []),

                {
                    type: "location",
                    required: true,
                    alignment: {xs: 12},
                    id: userType === "Submitter" ? ".city" : ".address",
                    hint: "city your are based to find hospitals nearby",
                    locationIds: {
                        latitude: ".geolocation.latitude",
                        longitude: ".geolocation.longitude",
                    },
                    autoComplete: "address-level2",
                    label: "City",
                },
            ] as FormItem[],
        [registrationToken, userType],
    )

    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    {userType === "Submitter" ? "Medical" : "Maker"} Register
                </Typography>

                <FormComponent
                    submitButtonTitle="Register"
                    formItems={formItems}
                    onUpdate={(id, value) => {
                        if (id === ".type") {
                            changeUserType(value)
                        }
                    }}
                    onSubmit={onSubmit}>
                    <Grid style={{marginTop: 10}} container justify="flex-end">
                        <Grid item>
                            <Link href="/account/login" variant="body2">
                                Already have an account? Login
                            </Link>
                        </Grid>
                    </Grid>
                </FormComponent>
            </div>
        </Container>
    )
}

export interface RegisterPageProps extends BasePageProps {}
export const RegisterPage: React.FC<RegisterPageProps> = ({updateSnack}) => {
    const [mutate] = useRegisterMutation({
        refetchQueries: [{query: MeDocument}],
        onCompleted: data => {
            if (data.register) {
                localStorage.setItem(tokenKey, data.register.token)
                goTo("/")
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
    })
    const onSubmit = useCallback(
        (
            args: MutationRegisterArgs & {
                confirmPassword: string
            },
        ) => {
            if (args.password === args.confirmPassword) {
                delete args.confirmPassword
                mutate({
                    variables: args,
                })
            } else {
                updateSnack({message: "passwords do not match"})
            }
        },
        [mutate, updateSnack],
    )

    return (
        <PageComponent
            showFooter={true}
            title="Register"
            rolePages={[
                {role: "Submitter", redirect: "/"},
                {role: "Volunteer", redirect: "/"},
            ]}>
            <RegisterPageView onSubmit={onSubmit} />
        </PageComponent>
    )
}

export default RegisterPage
