export interface LocationSearch {
    type: string
    licence: string
    features: LocationFeature[]
}

export interface LocationFeature {
    type: FeatureType
    properties: Properties
    bbox: number[]
    geometry?: Geometry
}

export interface Geometry {
    type: GeometryType
    coordinates: number[]
}

export enum GeometryType {
    Point = "Point",
}

export interface Properties {
    place_id: number
    osm_type: OsmType
    osm_id: number
    display_name: string
    place_rank: number
    category: Category
    type: string
    importance: number
    icon?: string
}

export enum Category {
    Boundary = "boundary",
    Place = "place",
    Railway = "railway",
}

export enum OsmType {
    Node = "node",
    Relation = "relation",
    Way = "way",
}

export enum FeatureType {
    Feature = "Feature",
}

export const searchLocations = async (query: string): Promise<LocationSearch> => {
    const response = await fetch(
        `https://nominatim.openstreetmap.org/search?q=${encodeURI(
            query,
        )}&format=geojson`,
    )
    return response.json()
}
