import {Typography} from "@material-ui/core"
import Avatar from "@material-ui/core/Avatar"
import Container from "@material-ui/core/Container"
import {makeStyles} from "@material-ui/core/styles"
import LockOutlinedIcon from "@material-ui/icons/LockOutlined"
import React, {useCallback, useMemo} from "react"
import {BasePageProps} from "../../App"
import FormComponent, {FormItem} from "../../components/form/form"
import PageComponent from "../../components/page"
import WrongPageComponent from "../../components/wrong-page"
import {
    MutationSubmitForgotPasswordArgs,
    useSubmitForgotPasswordMutation,
} from "../../graphql/schema.generated"
import {goTo, useQuery} from "../../services/utils"

const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
}))

export interface PasswordResetPageViewProps {
    onSubmit: (
        form: MutationSubmitForgotPasswordArgs & {
            confirmPassword: string
        },
    ) => Promise<void>
}

export const PasswordResetPageView: React.FC<PasswordResetPageViewProps> = ({
    onSubmit,
}) => {
    const classes = useStyles()

    const formItems = useMemo(
        () =>
            [
                {
                    type: "text",
                    required: true,
                    textType: "password",
                    alignment: {xs: 12},
                    id: ".password",
                    label: "Password",
                    autoComplete: "password",
                },
                {
                    type: "text",
                    required: true,
                    textType: "password",
                    alignment: {xs: 12},
                    id: ".confirmPassword",
                    label: "Confirm Password",
                    autoComplete: "password",
                },
            ] as FormItem[],
        [],
    )

    return (
        <Container component="main" maxWidth="xs">
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Change Password
                </Typography>

                <FormComponent
                    submitButtonTitle="Change Password"
                    formItems={formItems}
                    onSubmit={onSubmit}></FormComponent>
            </div>
        </Container>
    )
}

export interface PasswordResetPageProps extends BasePageProps {}
const PasswordResetPage: React.FC<PasswordResetPageProps> = ({updateSnack}) => {
    const [mutate] = useSubmitForgotPasswordMutation({
        onCompleted: data => {
            if (data.submitForgotPassword) {
                updateSnack({
                    message: "successfully changed password. redirecting...",
                    severity: "success",
                })
                setTimeout(() => {
                    goTo("/account/login")
                }, 1000)
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
    })

    const query = useQuery()
    const argToken = useMemo(() => query.get("token"), [query])

    const onSubmit = useCallback(
        async (
            args: MutationSubmitForgotPasswordArgs & {
                confirmPassword: string
            },
        ) => {
            if (args.password === args.confirmPassword && argToken) {
                delete args.confirmPassword
                args.token = argToken
                mutate({
                    variables: args,
                })
            } else {
                updateSnack({message: "passwords do not match"})
            }
        },
        [argToken, mutate, updateSnack],
    )

    return (
        <PageComponent
            showFooter={true}
            title="Password Reset"
            rolePages={[
                {role: "Submitter", redirect: "/"},
                {role: "Volunteer", redirect: "/"},
            ]}>
            {argToken ? (
                <PasswordResetPageView onSubmit={onSubmit} />
            ) : (
                <WrongPageComponent
                    text={"Invalid reset token"}></WrongPageComponent>
            )}
        </PageComponent>
    )
}

export default PasswordResetPage
