/* eslint-disable no-use-before-define */
import TextField, {TextFieldProps} from "@material-ui/core/TextField"
import Autocomplete from "@material-ui/lab/Autocomplete"
import React, {useEffect, useState} from "react"
import useDebounce from "../hooks/useDebounce"
import {LocationFeature, searchLocations} from "../services/location-search"

export type OsmAutocompleteProps<T> = {
    onLocationSelect?: (feature: LocationFeature | null) => void
    defaultValue?: T
    freeSolo : boolean
    textProps?: TextFieldProps
}

export default function OsmAutocomplete(
    props: OsmAutocompleteProps<LocationFeature>,
) {
    const {onLocationSelect, defaultValue, textProps, freeSolo} = props
    const [text, changeText] = useState<string>("")
    const debouncedText = useDebounce(text, 500)
    const [options, changeOptions] = useState<LocationFeature[]>([])
    useEffect(() => {
        const search = async () => {
            if (debouncedText !== "") {
                const search = await searchLocations(debouncedText)
                changeOptions(() => {
                    return [...search.features]
                })
            }
        }
        search()
    }, [debouncedText])

    return (
        <Autocomplete
            defaultValue={defaultValue}
            freeSolo={freeSolo}
            id="combo-box-demo"
            filterOptions={x => x}
            onChange={(_: any, newValue: LocationFeature | null) => {
                if (onLocationSelect) {
                    onLocationSelect(newValue)
                }
            }}
            renderInput={params => (
                <TextField
                    {...params}
                    {...textProps}
                    onChange={({target: {value}}) => {
                        changeText(value)
                        if (onLocationSelect) {
                            //@ts-ignore
                            onLocationSelect({
                                properties: {
                                    display_name: value,
                                },
                            })
                        }
                    }}
                />
            )}
            options={options}
            getOptionLabel={option => option.properties.display_name}
        />
    )
}
