import {ButtonProps, Fade, makeStyles, Typography} from "@material-ui/core"
import AccountCircleIcon from "@material-ui/icons/AccountCircle"
import LockIcon from "@material-ui/icons/Lock"
import React, {useEffect, useState} from "react"
import {
    AllUserInfoFragment,
    useMeQuery,
    UserType,
} from "../graphql/schema.generated"
import {goTo} from "../services/utils"
import HideAppBar, {SideBarButton} from "./hide-app-bar"
import MenuComponent from "./menu"

export interface RolePage {
    component?: (user: AllUserInfoFragment) => React.ReactNode
    role: UserType
    redirect?: string
    title?: string
}

export interface PageComponentProps {
    title: string
    showFooter?: boolean
    disableHide?: boolean
    rolePages?: RolePage[]
    roleRedirect?: string
}

const useStyles = makeStyles(() => ({
    footer: {
        marginTop: "7vh",
        textAlign: "center",
    },
    container: {
        display: "flex",
        flexDirection: "column",
    },
}))

const PageComponent: React.FunctionComponent<PageComponentProps> = ({
    children,
    showFooter = false,
    disableHide = true,
    rolePages,
    title,
    roleRedirect,
}) => {
    const classes = useStyles()

    useEffect(() => {
        document.title = `Op Dynamo | ${title}`
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const [component, changeComponent] = useState<React.ReactNode>()
    const {error: meError, data: meData, loading} = useMeQuery()

    const loggedOutButtons = [
        {title: "Legal", path: "/legal", type: "bar-button"},
        {title: "About", path: "/about", type: "bar-button"},
        {
            title: "Contact",
            path: "/contact",
            type: "bar-button",
        },
        {title: "FAQ", path: "/faq"},
    ]

    useEffect(() => {
        if (!meError && rolePages && meData?.me?.user) {
            const userType = meData.me.user.type
            const rolePage = rolePages.find(rolePage => {
                return rolePage.role === userType
            })
            if (!rolePage) {
                if (roleRedirect) {
                    goTo(roleRedirect)
                }
            } else {
                if (rolePage.component) {
                    changeComponent(rolePage.component(meData.me.user))
                }
                if (rolePage.redirect) {
                    goTo(rolePage.redirect)
                }
            }
        } else if (!loading) {
            changeComponent(children)
        }
    }, [rolePages, meData, meError, roleRedirect, children, loading])

    return (
        <div>
            <HideAppBar
                disableHide={disableHide}
                inverted={false}
                homeButton={{
                    title: "Operation Dynamo",
                    path: "/",
                    type: "bar-button",
                }}
                buttons={
                    [
                        ...(meData?.me?.user
                            ? [
                                  {
                                      type: "element",
                                      element: (
                                          <MenuComponent
                                              name={`${meData.me.user.firstName} ${meData.me.user.lastName}`}
                                              icon={<AccountCircleIcon />}
                                              options={[
                                                  {
                                                      name: "Update Account",
                                                      url: "/account/update",
                                                  },
                                                  {
                                                      name: "Logout",
                                                      url: "/account/logout",
                                                  },
                                              ]}
                                          />
                                      ),
                                  },
                              ]
                            : [
                                  ...loggedOutButtons,
                                  {
                                      type: "bar-button",
                                      title: "Login",
                                      path: "/account/login",
                                      props: {
                                          startIcon: <LockIcon />,
                                          color: "primary",
                                          style: {
                                              color: "white",
                                              height: 35,
                                              marginLeft: 10,
                                          },
                                          variant: "contained",
                                      } as ButtonProps,
                                  },
                              ]),
                    ] as (SideBarButton & {
                        type: "element"
                        element: JSX.Element
                    })[]
                }>
                <div style={{marginTop: 40}}>{component}</div>
            </HideAppBar>
            <footer className={classes.footer}>
                {showFooter ? (
                    <Fade timeout={1000} in={showFooter}>
                        <Typography variant="caption" component="h3">
                             Operation Dynamo a product of Conva LLC. All
                            Rights Reserved
                        </Typography>
                    </Fade>
                ) : null}
            </footer>
        </div>
    )
}
export default PageComponent
