import {Button} from "@material-ui/core"
import BackupIcon from "@material-ui/icons/Backup"
import React, {useCallback, useState} from "react"
import Dropzone from "react-dropzone"
import {FileUploadFormItem} from "./form"

export type FormFileUploadItemProps = FileUploadFormItem & {
    handleChange: (id: string, value: File | string) => void
}

const FormFileUploadItem = ({
    id,
    label,
    handleChange,
    fileNameId,
}: FormFileUploadItemProps) => {
    const [fileName, changeFileName] = useState<string>()

    const onDrop = useCallback(
        acceptedFiles => {
            acceptedFiles.forEach((file: File) => {
                const reader = new FileReader()

                reader.onabort = () => console.log("file reading was aborted")
                reader.onerror = () => console.log("file reading has failed")
                reader.onload = () => {
                    changeFileName(file.name)
                    handleChange(id, file)
                    if (fileNameId) {
                        handleChange(fileNameId, file.name)
                    }
                }
                reader.readAsArrayBuffer(file)
            })
        },
        [fileNameId, handleChange, id],
    )

    return (
        <Dropzone multiple={false} maxSize={10 ** 20} onDrop={onDrop}>
            {({getRootProps, getInputProps}) => (
                <div
                    style={{
                        display: "flex",
                        justifyContent: "center",
                        marginTop: 20,
                    }}
                    {...getRootProps()}>
                    <input {...getInputProps()} />
                    <Button
                        fullWidth
                        variant="contained"
                        startIcon={<BackupIcon />}>
                        {fileName ? `${fileName}` : label}
                    </Button>
                </div>
            )}
        </Dropzone>
    )
}

export default FormFileUploadItem
