import {useState} from "react"
import {setNested} from "../services/utils"

const useForm = <
    T extends {
        [key: string]: any
    }
>(
    initial: Partial<T> | undefined = {},
) => {
    const [form, changeForm] = useState<Partial<T>>(initial)

    const updateForm = (path: string, value: any) => {
        changeForm(old => {
            //@ts-ignore
            const newV: Partial<T> = setNested(old, path, value)
            // console.log(newV)
            return newV
        })
    }
    return {form, updateForm}
}

export default useForm
