import React, {useMemo} from "react"
//@ts-ignore
import STLViewer from "stl-viewer"
import DXFViewer from "./dxf-viewer"
import theme from "./theme"

export type RenderType = "stl" | "dxf"

export interface PartRenderProps {
    url: string
    type: RenderType
    color?: string
    width: number
    height: number
}
const PartRender: React.FC<PartRenderProps> = ({
    type,
    url,
    color,
    width,
    height,
}) => {
    const viewer = useMemo(() => {
        return type === "dxf" ? (
            <DXFViewer
                width={width}
                height={height}
                source={{type: "url", url: url}}
                strokeColor={color || theme.palette.primary.light}
                strokeWidth={"0.5%"}
            />
        ) : (
            <STLViewer
                model={url}
                width={width}
                height={height}
                modelColor={color || theme.palette.primary.light}
                backgroundColor="white"
                rotate={true}
                orbitControls={true}
            />
        )
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return viewer
}

export default PartRender
