import {Typography} from "@material-ui/core"
import Container from "@material-ui/core/Container"
import {makeStyles} from "@material-ui/core/styles"
import React, {useCallback} from "react"
import {BasePageProps} from "../../App"
import FormComponent, {FormItem} from "../../components/form/form"
import PageComponent from "../../components/page"
import WrongPageComponent from "../../components/wrong-page"
import {
    AllUserInfoFragment,
    MutationCreateJobArgs,
    useCreateJobMutation,
} from "../../graphql/schema.generated"
import {goTo} from "../../services/utils"

const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(8),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
}))

export interface CreateJobPageViewProps {
    user: AllUserInfoFragment
    onSubmit: (form: MutationCreateJobArgs) => void
}

export const CreateJobPageView: React.FC<CreateJobPageViewProps> = ({
    onSubmit,
    user: {latitude, longitude, address},
}) => {
    const classes = useStyles()

    const formItems: FormItem[] = [
        {
            type: "slider",
            min: 5,
            max: 100,
            ending: "Parts",
            step: 5,
            defaultValue: 25,
            alignment: {xs: 12},
            id: ".quantity",
            label: "Quantity",
        },

        {
            type: "text",
            alignment: {xs: 12},
            id: ".description",
            label: "Description",
        },

        {
            type: "location",
            required: true,
            alignment: {xs: 12},
            id: ".address",
            defaultValue: {
                properties: {
                    display_name: address,
                },
                geometry: {
                    coordinates: [longitude, latitude],
                },
            },
            locationIds: {
                latitude: ".geolocation.latitude",
                longitude: ".geolocation.longitude",
            },
            label: "Shipping Address",
        },

        {
            type: "file-upload",
            required: true,
            alignment: {xs: 12},
            id: ".file",
            fileNameId: ".name",
            label: "Upload STL",
        },
    ]

    return (
        <Container component="main" maxWidth="xs">
            <div className={classes.paper}>
                <Typography component="h1" variant="h5">
                    Create Print Jobs
                </Typography>

                <FormComponent
                    submitButtonTitle="Create Jobs"
                    formItems={formItems}
                    onSubmit={onSubmit}
                />
            </div>
        </Container>
    )
}

export interface CreateJobPageProps extends BasePageProps {}
export const CreateJobPage: React.FC<CreateJobPageProps> = ({updateSnack}) => {
    const [mutate] = useCreateJobMutation({
        onCompleted: data => {
            if (data.createJob) {
                goTo("/")
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
    })
    const onSubmit = useCallback(
        (args: MutationCreateJobArgs) => {
            mutate({
                variables: args,
            })
        },
        [mutate],
    )

    return (
        <PageComponent
            showFooter={true}
            title="Create Job"
            rolePages={[
                {
                    role: "Submitter",
                    component: user => (
                        <CreateJobPageView onSubmit={onSubmit} user={user} />
                    ),
                },
            ]}>
            <WrongPageComponent
                text={
                    "Must be logged in as a Submitter to create a job"
                }></WrongPageComponent>
        </PageComponent>
    )
}

export default CreateJobPage
